@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}
hr {
  margin: 1rem;
}
.App {
  font-family: sans-serif;
  text-align: center;
  display: flex;
  align-items: flex-start;
}
h1 {
  font-family: cursive;
  color: #56fd73;
  font-size: 1.5rem;
  margin: 1rem;
}

.tag {
  width: 50%;
}
.title {
  text-align: center;
  font-family: cursive;
  min-height: 7rem;
  padding-top: 2rem;
  cursor: pointer;
}
.title h1 {
  margin-bottom: 0;
  color: #56fd73;
  font-size: 2rem;
  text-shadow: 2px 2px 8px #56fd73;

}
.body {
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 2rem;
}
.otherNotes {
  display: grid;
  grid-template-columns: auto auto auto;
}

.pinnedNotes {
  display: grid;
  grid-template-columns: auto auto auto;
}
.footer {
  left: 0;
  bottom: 0;
  width: 100%;
  color: black;
  text-align: center;
  font-family: cursive;
}
.footer div{
  display:flex;
  justify-content: center;
  
}

.fa-twitter {
  background:white;
  color: black;
  padding:1rem;
  margin:1rem;
  text-decoration: none;
}

.fa-linkedin {
  background: white;
  color: black;
  padding:1rem;
  margin:1rem;
  text-decoration: none;
}

.fa-github {
  background: white;
  color: black;
  padding:1rem;
  margin:1rem;
  text-decoration: none;
}
.fa {
  padding: 20px;
  font-size: 30px;
  width: 50px;
  text-align: center;
  text-decoration: none;
  margin: 5px 2px;
}

.note {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    width: 18rem;
    margin: 0.1rem;
    border-radius: 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    height: auto;
  }
  
  .note .colorPicker TwitterPicker {
    position: absolute;
  }
  
  .note h2,
  p {
    font-family: cursive;
    padding: 0.5rem;
  }
  .note small {
    padding: 0.5rem;
    font-family: "Times New Roman", Times, serif;
  }
  .note .note-text {
    min-height: 4rem;
    overflow-wrap: break-word;
    word-wrap: break-word;
    -webkit-hyphens: auto;
        -ms-hyphens: auto;
            hyphens: auto;
  }
  
  .note Label {
    font-family: cursive;
  }
  .note Select {
    font-family: cursive;
    margin: 0.25rem;
    border: none;
  }
  .note input {
    margin: 0.25rem;
    border: none;
    font-family: cursive;
  }
  .note textarea {
    border: none;
    font-family: cursive;
  }
  .note button {
    margin: 0.25rem;
    font-family: cursive;
    border: thin black solid;
    border-radius: 8px;
    padding: 0.2rem;
    margin: 0.2rem;
    cursor: pointer;
  }
  
.tags {
    display: flex;
    flex-direction: column;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin: 2rem;
    padding-left: 0rem;
    padding-right: 0rem;
    padding-bottom: 3rem;
  }
  
  .tags ul {
    list-style-type: none;
    font-family: cursive;
  }
  li {
    cursor: pointer;
  }
  .tags input {
    height: 2rem;
    border-radius: 5px;
    text-align: center;
    font-family: cursive;
    margin-top: 2rem;
    margin-bottom: 2rem;
  }

  .tags button {
    font-family: cursive;
    cursor: pointer;
    margin:1rem;
  }
  .deleteList li:hover{

    color:red;

  }
  
.tagFilterBody{
    display:flex;
    flex-direction:column;
}

.tagFilter{
    display: grid;
    grid-template-columns: auto auto auto;
    margin:2rem;
    padding:2rem;

}
